@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body{margin:0;font-family:'Roboto', sans-serif;height:100vh}


.Dossier{width:300px;height:300px;color:white;box-shadow:0px 2px 5px 0px rgba(0,0,0,0.55);background-size:contain}.Dossier .sortIcon{margin:0;padding:0;margin:10px}.Dossier .sortIcon,.Dossier .sortIcon:active{box-shadow:none}.Dossier .info{display:grid;grid-template-columns:80% 20%;grid-template-rows:50% 50%;position:relative;height:40%;padding:10px 20px;margin-top:34%}.Dossier .info .moreIcon{cursor:pointer;grid-row-start:2;grid-column-start:2;justify-self:flex-end;align-self:center}.Dossier .info p{grid-row-start:2;grid-column-start:1}

.ListeDossiers{display:grid;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:20px auto;grid-template-columns:300px 300px 300px 300px;grid-gap:20px}@media screen and (max-width: 1375px){.ListeDossiers{grid-template-columns:300px 300px 300px}}@media screen and (max-width: 930px){.ListeDossiers{grid-template-columns:300px 300px}}@media screen and (max-width: 635px){.ListeDossiers{grid-template-columns:300px}}

.Entete{display:flex;justify-content:space-between;background-color:#2b2b2b;color:white;padding:5px;padding-left:20px}.Entete .userInfo{display:flex}.Entete .userInfo .Avatar{margin:auto 15px}

.BoutonPlus{position:fixed !important;right:30px;bottom:30px}

